import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AccessGuard } from './services/guards/access.guard';
import { CustomLayoutComponent } from './modules/custom-layout/custom-layout/custom-layout.component';

const routes: Routes = [
  {
    path: '',
    component: CustomLayoutComponent,
    canActivate: [AccessGuard],
    runGuardsAndResolvers: 'always',
    children: [
      {
        path: '',
        loadChildren: () =>
          import('./pages/dashboard/dashboard.module').then(
            (m) => m.DashboardModule
          ),
      },

      {
        path: 'system',
        loadChildren: () =>
          import('./pages/voicebot/voicebot.module').then((m) => m.AdminModule),
      },

      {
        path: '',
        redirectTo: '',
        pathMatch: 'full',
      },
    ],
  },
  {
    path: 'login',
    loadChildren: () =>
      import('./pages/login/login.module').then((m) => m.LoginModule),
  },
  {
    path: 'logout',
    loadChildren: () =>
      import('./pages/logout/logout.module').then((m) => m.LogoutModule),
  },
  {
    path: 'selector_cliente',
    loadChildren: () =>
      import('./pages/client-selector/client-selector.module').then(
        (m) => m.ClientSelectorModule
      ),
  },

  {
    path: '404',
    loadChildren: () =>
      import('./modules/errors/error-404/error-404.module').then(
        (m) => m.Error404Module
      ),
  },
  {
    path: '500',
    loadChildren: () =>
      import('./modules/errors/error-500/error-500.module').then(
        (m) => m.Error500Module
      ),
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      preloadingStrategy: PreloadAllModules,
      scrollPositionRestoration: 'enabled',
      relativeLinkResolution: 'corrected',
      anchorScrolling: 'enabled',
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
