import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  UrlTree,
  Router,
} from '@angular/router';
import { Observable } from 'rxjs';
import { WhareService } from '../wharehouse/wharehouse.service';

@Injectable({
  providedIn: 'root',
})
export class AccessGuard implements CanActivate {
  constructor(
    private router: Router,
    private whareHouseService: WhareService
  ) {}

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    const selectedWharehouse =
      this.whareHouseService.selectedWharehouse$.getValue();

    if (selectedWharehouse === null) {
      this.router.navigate(['/selector_cliente']);
    } else {
      return true;
    }
  }
}
