import {
  Component,
  Inject,
  LOCALE_ID,
  Renderer2,
  OnInit,
  IterableDiffers,
} from '@angular/core';
import { ConfigService } from '../@vex/services/config.service';
import { Settings } from 'luxon';
import { DOCUMENT } from '@angular/common';
import { Platform } from '@angular/cdk/platform';
import { NavigationService } from '../@vex/services/navigation.service';
import { LayoutService } from '../@vex/services/layout.service';
import { ActivatedRoute } from '@angular/router';
import { filter, map } from 'rxjs/operators';
import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { SplashScreenService } from '../@vex/services/splash-screen.service';
import { Style, StyleService } from '../@vex/services/style.service';
import { ConfigName } from '../@vex/interfaces/config-name.model';
import { environment } from '../environments/environment';
import { UtilService } from './services/utils/util/util.service';
import { fadeInUp400ms } from '../@vex/animations/fade-in-up.animation';

@Component({
  selector: 'vex-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [fadeInUp400ms],
})
export class AppComponent implements OnInit {
  title = 'Wharehouse';
  Style = Style;
  floatData = [];

  constructor(
    private configService: ConfigService,
    private styleService: StyleService,
    private renderer: Renderer2,
    private platform: Platform,
    @Inject(DOCUMENT) private document: Document,
    @Inject(LOCALE_ID) private localeId: string,
    private layoutService: LayoutService,
    private route: ActivatedRoute,
    private utilService: UtilService,
    private navigationService: NavigationService,
    private iterableDiffers: IterableDiffers,
    private splashScreenService: SplashScreenService // private basehttpService: BasehttpService
  ) {
    Settings.defaultLocale = this.localeId;

    if (this.platform.BLINK) {
      this.renderer.addClass(this.document.body, 'is-blink');
    }

    this.styleService.setStyle(Style.default);
    this.configService.setConfig(ConfigName.apollo);
    /**
     * Customize the template to your needs with the ConfigService
     * Example:
     *  this.configService.updateConfig({
     *    sidenav: {
     *      title: 'Custom App',
     *      imageUrl: '//placehold.it/100x100',
     *      showCollapsePin: false
     *    },
     *    showConfigButton: false,
     *    footer: {
     *      visible: false
     *    }
     *  });
     */

    /**
     * Config Related Subscriptions
     * You can remove this if you don't need the functionality of being able to enable specific configs with queryParams
     * Example: example.com/?layout=apollo&style=default
     */
    this.route.queryParamMap
      .pipe(
        map(
          (queryParamMap) =>
            queryParamMap.has('rtl') &&
            coerceBooleanProperty(queryParamMap.get('rtl'))
        )
      )
      .subscribe((isRtl) => {
        this.document.body.dir = isRtl ? 'rtl' : 'ltr';
        this.configService.updateConfig({
          rtl: isRtl,
        });
      });

    this.route.queryParamMap
      .pipe(filter((queryParamMap) => queryParamMap.has('layout')))
      .subscribe((queryParamMap) =>
        this.configService.setConfig(queryParamMap.get('layout') as ConfigName)
      );

    this.route.queryParamMap
      .pipe(filter((queryParamMap) => queryParamMap.has('style')))
      .subscribe((queryParamMap) =>
        this.styleService.setStyle(queryParamMap.get('style') as Style)
      );

    if (environment.production) {
      if (window) {
        window.console.log =
          window.console.warn =
          window.console.info =
            function () {
              // Don't log anything.
            };
      }
    }
  }

  ngOnInit() {
    this.utilService.event_floating_new$.subscribe((data: any) => {
      console.log(
        'TOTAL INSERT',
        this.floatData.length,
        this.floatData.length >= 1 ? 135 : 0
      );

      let add = this.floatData.length >= 1 ? 135 : 0;
      let floatDataTemp = {
        active: data.active ? data.active : false,
        loading: data.loading ? data.loading : false,
        msg: data.msg ? data.msg : '',
        title: data.title ? data.title : '',
        id: data.id ? data.id : '',
        bottom: 20 + add,
        error: data.error ? data.error : false,
      };

      this.floatData.push(floatDataTemp);
      // console.log(this.floatData);
    });

    this.utilService.event_floating_edit$.subscribe((data: any) => {
      let indexFound = this.floatData.findIndex((fd) => fd.id === data.id);
      if (indexFound >= 0) {
        Object.keys(data).forEach((k) => {
          this.floatData[indexFound][k] = data[k];
        });
      }
      //console.log('UPDATE', this.floatData[indexFound]);
    });

    this.utilService.event_floating_close$.subscribe((id: any) => {
      let indexFound = this.floatData.findIndex((fd) => fd.id === id);
      console.log('FOUND', indexFound, id, this.floatData);

      if (indexFound >= 0) {
        this.floatData[indexFound].active = false;
        this.floatData.splice(indexFound, 1);
        this.recalculatingFloating();
      }
      // console.log('CLOSE', this.floatData[indexFound]);
    });
  }

  recalculatingFloating() {
    this.floatData.forEach((fd, index) => {
      let add = index > 0 ? 135 * (index + 1) : 0;
      this.floatData[index].bottom = 20 + add;
    });
  }
}
