import {
  AfterViewInit,
  Component,
  OnInit,
  Input,
  IterableDiffers,
} from '@angular/core';
import { fadeInUp400ms } from '../../../../@vex/animations/fade-in-up.animation';
import {
  MAT_FORM_FIELD_DEFAULT_OPTIONS,
  MatFormFieldDefaultOptions,
} from '@angular/material/form-field';
import { stagger40ms } from '../../../../@vex/animations/stagger.animation';

import icSend from '@iconify/icons-ic/twotone-send';
import icList from '@iconify/icons-ic/baseline-list-alt';
import baselineFontDownload from '@iconify/icons-ic/round-font-download';
import baselineLooksOne from '@iconify/icons-ic/baseline-looks-one';

@Component({
  selector: 'floating-base',
  templateUrl: './floating.component.html',
  styleUrls: ['./floating.component.scss'],
  animations: [fadeInUp400ms, stagger40ms],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class FloatingComponent implements OnInit, AfterViewInit {
  public headersAvaiable: any[] = [];
  @Input() data = {
    active: false,
    loading: false,
    msg: '',
    title: '',
    id: '',
    bottom: 50,
    error: false,
  };

  icSend = icSend;
  icByAlpha = baselineFontDownload;
  icNumericDownAlt = baselineLooksOne;
  icList = icList;

  constructor(private iterableDiffers: IterableDiffers) {}

  ngOnInit(): void {
    console.log(this.data);
  }
  ngAfterViewInit(): void {
    console.log(this.data);
  }
}
