import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, switchMap } from 'rxjs/operators';
import { getInterpolationArgsLength } from '@angular/compiler/src/render3/view/util';

@Injectable({
  providedIn: 'root',
})
export class BasehttpService {
  private headers_default: HttpHeaders;

  constructor(private httpClient: HttpClient) {
    this.headers_default = new HttpHeaders()
      .set('content-type', 'application/json')
      .set('Access-Control-Allow-Origin', '*')
      .set('mibot-request', '1');
  }

  public get(url: string) {
    return new Promise((resolve, reject) => {
      this.httpClient.get(url, { headers: this.headers_default }).subscribe(
        (result: any) => {
          if (result.data) {
            //console.log('bien');
            resolve(result);
          }
        },
        (error) => {
          //console.log('mal 2');
          // console.error('error:');
          // console.error(err);
          //if (err.error) err.message = `${err.error}`;
          //console.log('mal 3');
          reject(error);
        }
      );
    });
    /* return this.httpClient.get(url).toPromise()
    .then(resp => resp)
    .catch(err => err);*/
  }

  public post(url: string, objeto: Object): Promise<any> {
    return new Promise((resolve, reject) => {
      this.httpClient
        .post(url, objeto, { headers: this.headers_default })
        .subscribe(
          (result: any) => {
            resolve(result);
          },
          (error: any) => {
            reject(error);
          }
        );
    });

    /*return this.httpClient.post(objeto).toPromise()
    .then(resp => resp)
    .catch(err => err);*/
  }

  public put(url: string, objeto?: Object): Promise<any> {
    return new Promise((resolve, reject) => {
      this.httpClient
        .put(url, objeto, { headers: this.headers_default })
        .subscribe(
          (result: any) => {
            resolve(result);
          },
          (error: any) => {
            reject(error);
          }
        );
    });

    /*return this.httpClient.post(objeto).toPromise()
    .then(resp => resp)
    .catch(err => err);*/
  }

  public postFormData(url: string, objeto: Object): Promise<any> {
    const headers_default = new HttpHeaders()
      .set('content-type', 'multipart/form-data; boundary=X-INSOMNIA-BOUNDARY')
      .set('Access-Control-Allow-Origin', '*')
      .set('mibot-request', 'multi-part');
    return new Promise((resolve, reject) => {
      this.httpClient.post(url, objeto, { headers: headers_default }).subscribe(
        (result: any) => {
          resolve(result);
        },
        (error: any) => {
          reject(error);
        }
      );
    });

    /*return this.httpClient.post(objeto).toPromise()
    .then(resp => resp)
    .catch(err => err);*/
  }

  public update(url: string, objeto: Object): Promise<any> {
    return new Promise((resolve, reject) => {
      this.httpClient
        .put(url, objeto, { headers: this.headers_default })
        .subscribe(
          (result: any) => {
            resolve(result);
          },
          (error: any) => {
            reject(error);
          }
        );
    });

    /*return this.httpClient.put(objeto).toPromise()
    .then(resp => resp)
    .catch(err => err);*/
  }

  public delete(url: string, objeto?: Object): Promise<any> {
    return new Promise((resolve, reject) => {
      this.httpClient.delete(url, { headers: this.headers_default }).subscribe(
        (result: any) => {
          resolve(result);
        },
        (error: any) => {
          reject(error);
        }
      );
    });
    /* return this.httpClient.delete(url).toPromise()
    .then(resp => resp)
    .catch(err => err);*/
  }

  public post_custom(url: string, objeto: Object): Promise<any> {
    return new Promise((resolve, reject) => {
      this.httpClient.post(url, objeto).subscribe(
        (result: any) => {
          resolve(result);
        },
        (error: any) => {
          reject(error);
        }
      );
    });
  }

  public get_custom(url: string) {
    return new Promise((resolve, reject) => {
      this.httpClient.get(url).subscribe(
        (result: any) => {
          resolve(result);
        },
        (error: any) => {
          reject(error);
        }
      );
    });
  }

  public get_observable(url: string) {
    return this.httpClient.get<any>(url, { headers: this.headers_default });
  }
}
