import { Injectable } from '@angular/core';
import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import * as moment from 'moment-timezone';
import { LS } from 'src/app/interfaces/static-data/LS';
import { auth } from 'firebase/app';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class UtilService {
  private horizontalPosition: MatSnackBarHorizontalPosition = 'right';
  private verticalPosition: MatSnackBarVerticalPosition = 'top';

  private CODE_TOKEN_NOT_FOUND = 403;
  private CODE_TOKEN_INVALID = 401;
  private CODE_TOKEN_EXPIRED = 403;

  private _eventFloatingNew = new Subject();
  event_floating_new$ = this._eventFloatingNew.asObservable();

  private _eventFloatingEdit = new Subject();
  event_floating_edit$ = this._eventFloatingEdit.asObservable();

  private _eventFloatingClose = new Subject();
  event_floating_close$ = this._eventFloatingClose.asObservable();

  constructor(private _snackBar: MatSnackBar, private router: Router) {
    moment.locale('es');
    console.log(moment.tz.guess());
    moment().tz(moment.tz.guess());
  }

  // momment
  obtenerFechaActualYTiempo() {
    // DE LA PC DE USUARIO
    return moment().tz('America/Lima').format('YYYY-MM-DD H:MM:SS');
    // return moment().format('YYYY-MM-DD H:MM:SS');
  }

  obtenerFechaFile() {
    return moment().tz('America/Lima').format('YYYY-MM-DD');
  }

  //Hora
  getTime(timeChat: string) {
    return moment(timeChat).format('h:mm:ss a');
  }

  formatDate(date: string) {
    return moment(date, 'YYYY-MM-DDTHH:mm:ss.000000Z').format(
      'DD-MM-YYYY HH:mm:ss'
    );
  }

  //Fecha - Hora
  getFullTime(timeChat: string) {
    return moment(timeChat).format('DD-MM-YYYY h:mm:ss a');
  }

  //Fecha
  getDate(timeChat: string) {
    return moment(timeChat).format('DD-MM-YYYY');
  }

  getDateFormat(timeChat: string, format: string) {
    return moment(timeChat).format(format);
  }

  /**
   * Recibe un string fecha en un formato dado y retorna un Date
   * @param fecha : string {1999-11-11}
   * @param formato : string {DD/MM/AAAA}
   * Buscar ms ejemplos en: https://momentjs.com/docs/
   */
  fomatearFechaString(fecha, formato): Date {
    return moment(fecha, formato).toDate();
  }

  formatearDateToStringYYYYMMDD(fecha: Date) {
    return fecha ? fecha.toISOString().slice(0, 10) : null;
  }

  timesChat(timeChat: string) {
    return moment(timeChat).fromNow();
  }

  checkInRange(start: string, end: string, now: string) {
    const dateStart = this.fomatearFechaString(start, 'YYYY-MM-DD');
    const dateEnd = this.fomatearFechaString(end, 'YYYY-MM-DD');
    const dateNow = this.fomatearFechaString(now, 'YYYY-MM-DD');
    if (dateNow >= dateStart && dateNow <= dateEnd) {
      return true;
    }
    return false;
  }

  // errores

  handleError(error: any, contexto?) {
    console.log(error);
    console.log(error.error.errors);
    contexto.cargando && (contexto.cargando = false);

    // chatbot, mail
    console.log(error.error.hasOwnProperty('errors'));
    if (error.error.hasOwnProperty('errors')) {
      if (error.error.errors[0].code == 'auth/id-token-expired') {
        localStorage.removeItem('user');
        sessionStorage.removeItem('workspace_token');
        this.router.navigate(['/login']);
      } else if (
        error.error.errors[0].code == 'auth/module-unauthorized' &&
        error.status == '423'
      ) {
        this.openSnackBar(
          'Lo sentimos, no tiene acceso a este modulo',
          LS.TAG_AVISO
        );
        // this.router.navigate(['/']);
        return;
      }
      console.log(this.router.url);

      if (this.router.url != '/' && this.router.url != '') {
        this.router.navigate(['/']);
        return;
      } else {
        console.log(error.error.errors);
        error.error?.errors.forEach((error) => {
          console.log(error.msg);
          this.openMessage(error.msg, 'Error', 'Error');
        });
      }
    }

    // console.log(error.error.errors[0].code )

    if (!error?.error.hasOwnProperty('data')) {
      if (error?.message == 'Http failure response') {
        console.log(error.message);
        this.openSnackBar(error?.message, LS.TAG_ERROR);
      }
      return;
    }

    //
    if (error?.error?.data === null) {
      this.showErrorHttp(error);
    }
    if (error?.error?.data.hasOwnProperty('errors')) {
      let error_msj = '';
      let retardo = 0;

      // ver si el code del token se ha vencido
      if (error.error.data.errors[0].code != 'auth/id-token-expired') {
        error.error.data.errors.forEach((e, index) => {
          error_msj = `${e.param}: ${e.msg} `;
          setTimeout(() => {
            this.openSnackBar(error_msj, LS.TAG_ERROR);
          }, index * retardo);
          retardo = 6000;
        });
      } else {
        localStorage.removeItem('user');
        sessionStorage.removeItem('workspace_token');
        this.router.navigate(['/login']);
      }
    } else {
      switch (error.status) {
        case 400: // error bd interno de mibot
          this.openSnackBar(error.message, LS.TAG_AVISO);
          // this.erroresMibot(error.data.errors);
          break;
        case 401:
        case 403:
          this.openSnackBar('No autorizado', LS.TAG_AVISO);
          this.router.navigate(['login']);
        case 423:
          this.openSnackBar(
            'Lo sentimos, no tiene acceso a este modulo',
            LS.TAG_AVISO
          );
          break;
        case 404:
          this.openSnackBar('Error interno, vuelva a intentarlo', LS.TAG_AVISO);
          //this.router.navigate(['404']);
          break;
        case 500:
          this.openSnackBar('Error interno, vuelva a intentarlo', LS.TAG_AVISO);
          break;
        case 0:
          this.openSnackBar('No hay conexin con el servidor', LS.TAG_AVISO);
          break;
        default:
          this.openSnackBar(error.message, LS.TAG_ERROR);
          break;
      }
    }
  }

  showErrorHttp(error) {
    switch (error.status) {
      case 400: // error bd interno de mibot
        this.openSnackBar(error.message, LS.TAG_AVISO);
        // this.erroresMibot(error.data.errors);
        break;
      case 401:
      case 403:
        this.openSnackBar('No autorizado', LS.TAG_AVISO);
        this.router.navigate(['login']);
        // this.authService.LogOut();
        break;
      case 423:
        this.openSnackBar(
          'Lo sentimos, no tiene acceso a este modulo',
          LS.TAG_AVISO
        );
        break;
      case 404:
        this.openSnackBar('Error interno, vuelva a intentarlo', LS.TAG_AVISO);
        //this.router.navigate(['404']);
        break;
      case 500:
        this.openSnackBar('Error interno, vuelva a intentarlo', LS.TAG_AVISO);
        break;
      case 0:
        this.openSnackBar('No hay conexin con el servidor', LS.TAG_AVISO);
        break;
      default:
        this.openSnackBar(error.message, LS.TAG_ERROR);
        break;
    }
  }

  openMessage(
    message: string,
    action: string,
    panelClass?: 'Primary' | 'Error'
  ) {
    this.openSnackBar(message, action, panelClass);
  }

  private openSnackBar(message: string, action: string, panelClass?: string) {
    this._snackBar.open(message, action, {
      duration: 6000,
      horizontalPosition: this.horizontalPosition,
      verticalPosition: this.verticalPosition,
      panelClass: panelClass,
    });
  }

  playAudioChat() {
    var audio = new Audio(LS.URI_AUDIO_MESSAGE);
    audio.play();
  }

  getSessionUidClient(): string {
    return JSON.parse(sessionStorage.getItem('mibot_session')).client_uid;
  }

  getSessionUidProyect(): string {
    return JSON.parse(sessionStorage.getItem('mibot_session')).project_uid;
  }

  uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c == 'x' ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  }

  ValidateEmail(mail) {
    if (
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
        mail
      )
    ) {
      return true;
    }
    this.openMessage('Favor verfique el formato del email', 'Error');
    return false;
  }

  public codeStatusLogout(code: number) {
    return (
      code === this.CODE_TOKEN_NOT_FOUND ||
      code === this.CODE_TOKEN_INVALID ||
      code === this.CODE_TOKEN_EXPIRED
    );
  }

  public megaToByte(mg: number) {
    return 1048576 * mg;
  }

  newFloating(data) {
    this._eventFloatingNew.next(data);
  }
  editFloating(data) {
    this._eventFloatingEdit.next(data);
  }
  closeFloating(data) {
    this._eventFloatingClose.next(data);
  }

  getProyectSession() {
    let mibotSession = JSON.parse(sessionStorage.getItem('mibot_session'));

    let userClient = JSON.parse(sessionStorage.getItem('user_client'));

    const proyect = userClient.billing_accounts[0].projects.find(
      (p) => p.uid === mibotSession.project_uid
    );

    return proyect;
  }

  get isAdminUser(): boolean {
    const user = JSON.parse(sessionStorage.getItem('user'));
    if (user.user.rol === 'admin') return true;

    return false;
  }
}
