import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';

import { catchError, map } from 'rxjs/operators';
import { Warehouse } from 'src/app/interfaces/wharehouse/wharehouse';

@Injectable({
  providedIn: 'root',
})
export class WhareService {
  private http: string = 'http://127.0.0.1:8000/api';
  selectedWharehouse$: BehaviorSubject<Warehouse>;
  constructor(private httpClient: HttpClient) {
    this.selectedWharehouse$ = new BehaviorSubject(null);

    this.selectedWharehouse$.pipe;
  }

  getWharehouse(): Observable<any> {
    let httpUrl = `${this.http}/warehouse`;
    return this.httpClient.get<FormData>(httpUrl);
  }

  createWharehouse(name: string, address: string): Observable<any> {
    let httpUrl = `${this.http}/warehouse/`;
    return this.httpClient.post(httpUrl, {
      name: name,
      address: address,
    });
  }

  selectWhareHouse(warehouse: Warehouse) {
    console.log({ warehouse });
    this.selectedWharehouse$.next(warehouse);
  }
}
